import {Form, Input} from 'antd-mobile'

export const AccountItem = ({txt = ''}) => {
  return (
    <Form.Item
      name='account'
      rules={[
        {
          required: true,
          message: `${txt}`
        },
      ]}
    >
      <Input
        clearable
        placeholder={`${txt}`}
      />
    </Form.Item>
  )
}
