import {Form, Input} from 'antd-mobile'
import {CodeWrap} from "./components";

export const CodeItem = ({txt = '', form, type}) => {
  return (
    <Form.Item
      name='code'
      extra={
        <CodeWrap form={form} type={type} />
      }
      rules={[
        {
          required: true,
          message: `${txt}`
        }
      ]}
    >
      <Input
        maxLength={6}
        clearable
        placeholder={`${txt}`}
      />
    </Form.Item>
  )
}
