import {Form, Input} from 'antd-mobile'
import {mobileRex} from "@/utils";

export const MobileItem = ({txt = ''}) => {
  return (
    <Form.Item
      name='mobile'
      rules={[
        {
          required: true,
          message: `${txt}`
        },
        {
          pattern: mobileRex,
          message: `${txt}`
        }
      ]}
    >
      <Input
        maxLength={11}
        clearable
        placeholder={`${txt}`}
      />
    </Form.Item>
  )
}

